@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
.homepage {
    width: 90%;
    margin: 0 auto;
}
.products-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(9rem, 1fr));
    grid-gap: 1.5rem;
    gap: 1.5rem;
}

@media screen and (min-width: 768px) {
    .products-list {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(13rem, 1fr));
        grid-gap: 1.5rem;
        gap: 1.5rem;
    }   
}

@media screen and (min-width: 1200px) {
    .products-list {
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(15.5rem, 1fr));
        grid-gap: 2rem;
        gap: 2rem;
    }   
}
.button {
    background-color: #131410;
    color: white;
    width: 100%;
    border: 2px solid transparent;
    cursor: pointer;

}

.button:hover {
    -webkit-filter: brightness(1.7);
            filter: brightness(1.7);
}

.button.sm {
    
}
.button.md {
    font-size: 1rem;
    padding: 0.5rem;
    border-radius: 0.5rem;

}
.button.lg {
    font-size: 1.2rem;
    padding: 0.7rem;
    border-radius: 0.7rem;

}
.product-card {
  background-color: white;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  overflow: hidden;
  /* box-shadow: 0 0 10px gray; */
  background: #e0e0e0;
box-shadow:  7px 7px 19px #bebebe,
             -7px -7px 19px #ffffff;}

.product-card__img {
  height: 70%;
  overflow: hidden;
}

.product-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-card__info {
  text-align: center;
  padding: 1rem 0.8rem 1.3rem;
  display: flex;
  flex-direction: column;
  grid-gap: 0.5rem;
  gap: 0.5rem;
}

.product-card__info h4 {
  font-size: 1.2em;
  font-weight: 500;
}

.product-card__info p {
  font-size: 1.3rem;
  font-weight: 600;
}

.header {
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    padding: 3rem 0;
    grid-gap: 3rem;
    gap: 3rem;
}

.header__title {
    /* background-color: orange; */
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.btn-showShoppingCart {
    background-color: white;
    padding: 0.4rem;
    border: none;
    border-radius: 0.7rem;
    position: relative;
}

.btn-showShoppingCart .tooltip {
    background-color: black;
    border-radius: 50%;
    position: absolute;
    height: 1.1rem;
    width: 1.1rem;
    color: white;
    top: -20%;
    right: -20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.header__search {
    /* background-color: blue; */
    align-self: center;
}

.form-search {
    background-color: white;
    width: 20rem;
    height: 3rem;
    border-radius: 0.5rem;
    display: flex;
    align-items: center;
    overflow: hidden;
    box-shadow:  10px 10px 20px #afafaf,
                 -10px -10px 20px #ffffff;
}

.form-search input {
    border: none;
    padding: 0 0.7rem;
    height: 100%;
    width: 100%;
    font-size: 1.05rem;
}

.form-search span {
    display: flex;
    align-items: center;
    padding: 0 0.7rem;
    height: 100%;
    background-color: transparent;
    border: none;
}
.cart-item {
    display: flex;
    align-items: center;
}

.cart-item__img {
    width: 5rem;
    height: 5rem;
    border-radius: 1.3rem;
    overflow: hidden;
}

.cart-item__img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}


.cart-item__info {
    flex: 1 1;
    padding-left: 1.5rem;
}

.cart-item__info > div {
    padding: 0.5rem 0;
}

.cart-item__header {
    font-weight: 500;
    font-size: 1.2rem;
    display: flex;
    justify-content: space-between;
}

.btn-delete-item {
    background-color: transparent;
    border: none;
    color: rgba(0, 0, 0, 0.616);
}

.cart-item__options {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: 500;
}

.cart-item__price {
    font-size: 1.2rem;
}

.cart-item__quantity {
    display: flex;
    align-items: center;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}


.btn-quantity {
    width: 1.4rem;
    height: 1.4rem;
    border-radius: 50%;
    border: 1px solid rgba(0, 0, 0, 0.678);
    background-color: transparent;
    font-weight: 700;
    font-size: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn-quantity.add {
    background-color: black;
    border-color: black;
    color: white;
    font-size: 0.88rem;
    font-weight: 600;
}

.shopping-cart {
  background-color: white;
  position: fixed;
  height: 100vh;
  width: 100%;
  padding: 1rem;
  top: 0;
  right: -100%;
  z-index: 9;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: all 0.3s ease-in;
}

.shopping-cart.--show {
  right: 0%;
}

.shopping-cart__header {
  text-align: center;
  padding: 1rem 0 1.5rem;
  position: relative;
  font-size: 1.2rem;
  border-bottom: 1px solid rgb(187, 187, 187);
}

.btn-hide-cart {
  border: none;
  position: absolute;
  left: -0.5rem;
  top: 0.6rem;
  background-color: transparent;
  color: black;
}

.shopping-cart__body {
  overflow-y: auto;
  flex: 1 1;
  padding: 1rem 1rem 1rem 0;
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.shopping-cart__footer {
  border-top: 1px solid rgb(187, 187, 187);
  padding-top: 0.5rem;
}

.shopping-cart__footer .total {
  display: flex;
  justify-content: space-between;
  font-weight: 500;
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.shopping-cart__footer .total .total__info {
  display: flex;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
}

.total__info .cantidad-items {
  font-size: 1rem;
}

@media screen and (min-width: 1024px) {
  .shopping-cart {
    width: 22rem;
    
  }
  
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  /* background-color: #d8d7d8; */
  background-color: #e0e0e0;
}

