.product-card {
  background-color: white;
  display: flex;
  flex-direction: column;
  border-radius: 1rem;
  overflow: hidden;
  /* box-shadow: 0 0 10px gray; */
  background: #e0e0e0;
box-shadow:  7px 7px 19px #bebebe,
             -7px -7px 19px #ffffff;}

.product-card__img {
  height: 70%;
  overflow: hidden;
}

.product-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.product-card__info {
  text-align: center;
  padding: 1rem 0.8rem 1.3rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.product-card__info h4 {
  font-size: 1.2em;
  font-weight: 500;
}

.product-card__info p {
  font-size: 1.3rem;
  font-weight: 600;
}
